    import {mapState, mapGetters} from 'vuex';
    export default {
        name: 'app-phone-binding',
	    data() {
            return {
                code: '',
                value: '',

                activeIndex: 1,




                phoneNumber: null,
                verificationCode: null,
            }
	    },
	    props: {
            bind: {
                type: Boolean,
            },
            phone: {
                type: String,
            }
	    },
        computed: {
            ...mapState({
                wxapp_img: state => state.mallConfig.__wxapp_img,
				is_manual_mobile_auth: state => state.mallConfig.mall.setting.is_manual_mobile_auth
            }),
        },
	    created() {

            let _this= this;
            uni.login({
                scopes: 'auth_base',
                success(res) {
                    if (res.errMsg === 'login:ok') {
                        _this.code = res.code;
                    }
                }
            })

	    },
	    methods: {

            getPhoneNumber(e) {
                if (e.detail.errMsg === 'getPhoneNumber:fail user deny' || !this.code) return;
                let _this = this;
                this.$request({
	                method: 'post',
	                url: _this.$api.phone.binding,
	                data: {
                        encryptedData: e.detail.encryptedData,
		                iv: e.detail.iv,
		                code: _this.code,
	                }
                }).then(response => {
                    this.value = response.data.mobile;
                })
            },




















            bindPhone(data, i) {
                if (i === 1) {
                    if (this.value === '') return;
                    this.$emit('click', data);
                } else {
                    if (this.verificationCode === null) return;
                    this.$request({
                        url: this.$api.phone.binding,
	                    method: 'post',
                        data: {
                            mobile: this.phoneNumber,
	                        mobile_code: this.verificationCode,
                        }
                    }).then(res => {
                       if (res.code === 0) {
                           this.$emit('click', data);
                       } else if (res.code === 1) {
                           uni.showModal({
                               title: '提示',
                               content: res.msg,
                           })
                       }
                    });
                }
            },
            clearPhone(data) {
                this.$emit('click', data);
            },
            obtain() {
                if (this.phoneNumber === null) return;
                this.$request({
	                url: this.$api.phone.code,
	                data: {
                        mobile: this.phoneNumber,
	                }
                }).then(res => {
                    if (res.code === 0) {
                        uni.showModal({
                            title: '提示',
                            content: res.msg,
                        })
                    } else if (res.code === 1) {
                        uni.showModal({
	                        title: '提示',
	                        content: res.msg,
                        })
                    }
                })
            },
            setActive(data) {
                this.activeIndex = data;
            }
	    },
    }
